* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding-left: 5px;
  list-style-type: none;
  max-width: 320px;
}

.country-list-item {
  font: inherit;
  margin-bottom: 5px;

  display: flex;
  align-items: center;
  
  text-decoration: none;
}

.country-icon {
  margin-right: 10px;
}

.country-info-item{
  font-size: x-large;
}
.country-info-head{
  font-weight: bold;
  font-size: x-large;
}